import { blurInfo, parseQuery, Sentry, UA } from '@elt_common/common';
import { get } from 'lodash-es';

import { ClaimSource, DesensitizeType, EnvCode } from '@/const';

export const getUrlToken = (
  name: string,
  str: string = window.location.search,
) => {
  const reg = new RegExp(`(^|&)${name}=([^&]*)(&|$)`);
  const r = str.substr(1).match(reg);
  return r?.[2] ? decodeURIComponent(r[2]) : null;
};

export const handleDataMap = (
  data: string | string[] | undefined,
  map?: { [key: string]: string },
) => {
  if (!data) {
    return '';
  }
  if (Array.isArray(data)) {
    const res = data?.reduce((pre, next) => {
      pre.push(map?.[next] || next);
      return pre;
    }, [] as string[]);
    return res.join('、');
  }
  return map?.[data] || data;
};

/**
 * @param dataSource 数据源
 * @param templatePath 模版路径，格式'{{templatePath}}'
 * @param dataMap 数据映射
 */
export const getValue = (
  dataSource: any,
  templatePath: string,
  dataMap?: { [key: string]: string },
) => {
  const regExp = /\{\{(.+?)\}\}/g;
  if (regExp.test(templatePath)) {
    let hasValue = false;
    const result = templatePath.replace(regExp, (item: string) => {
      const replacePath = item.slice(2, item.length - 2);
      const res = get(dataSource, replacePath);
      hasValue = Boolean(res);
      return handleDataMap(res, dataMap);
    });
    return hasValue ? result : '';
  }
  return templatePath;
};
/**
 * 根据不同类型进行脱敏
 * @param type 脱敏类型
 * @param text 需要脱敏的文案
 */
export const desensitize = (type: DesensitizeType, text: string) => {
  switch (type) {
    case DesensitizeType.Passport:
      return blurInfo(text, { start: 2, end: 2 });
    case DesensitizeType.IdCard:
      return blurInfo(text, { start: 3, end: 4 });
    case DesensitizeType.Name:
      return blurInfo(text, { start: 0, end: text?.length - 1 });
    case DesensitizeType.Phone:
      return blurInfo(text, { start: 3, end: 4 });
    case DesensitizeType.BankCard:
      return blurInfo(text, { start: 3, end: 4 });
    default:
      return text;
  }
};
/** 除去字符串中的所有指定字符（串）
 * @param str 需要格式化的字符串
 * @param separator 需要删除的字符串，默认空格
 * @return 返回格式化后的字符串
 */
export const trimStr = (str: string, separator = ' ') => {
  if (!str) {
    return '';
  }
  return str.split(separator).join('');
};

/**
 * 获取普通对象中所有有值的属性
 * @param obj
 * @return 普通对象中所有有值的属性构成的新对像
 */
export const getValuedProp = (obj?: Record<string, any>) => {
  if (!obj) return undefined;
  const tmp: Record<string, any> = {};
  Object.keys(obj).forEach((key) => {
    if (obj[key]) {
      tmp[key] = obj[key];
    }
  });
  return tmp;
};

export const getEnvName = (env_code: string) => {
  if (env_code === EnvCode.JKX) {
    return '健康险小程序';
  } else if (env_code === EnvCode.LYB) {
    return '蓝医保小程序';
  } else if (env_code === EnvCode.TaiBaoApp) {
    return '太保APP';
  } else if (env_code === EnvCode.TaiBaoAppBXX) {
    return '太保APP';
  } else if (env_code === EnvCode.PFMP) {
    return '浦发小程序(健康关爱福利平台)';
  } else if (UA.isWXBrowser()) {
    return '微信浏览器';
  } else {
    return '外部浏览器';
  }
};
const switchSourceFromCode = (code: string) => {
  switch (code) {
    case EnvCode.JKX:
      return ClaimSource.JKX;
    case EnvCode.LYB:
      return ClaimSource.LYB;
    default:
      return ClaimSource.JKX;
  }
};
// 获取链接参数上的来源数据
export const getClaimSource = () => {
  try {
    const query = parseQuery();
    const reportParam = query?.reportExt
      ? { ...JSON.parse(decodeURIComponent(query.reportExt)) }
      : {};
    return {
      claimSource:
        reportParam?.claim_source ||
        switchSourceFromCode(reportParam?.env_code),
      envCode: reportParam?.env_code || '',
    };
  } catch (error) {
    console.error(error);
    Sentry.reportError('豚豚工具文件: getClaimSource函数解析处理链接参数错误', {
      error,
    });
    return {
      claimSource: ClaimSource.JKX,
      envCode: EnvCode.JKX,
    };
  }
};
type OptionTypes = {
  label: string;
  value: string | number;
};
export const getLabelByValue = (
  options: OptionTypes[],
  value: string | number,
) => {
  if (value && options?.length > 0) {
    return options.find((option) => option.value === value)?.label ?? '';
  }
  return '';
};
export const getValueByLabel = (options: OptionTypes[], label: string) => {
  if (label && options?.length > 0) {
    return (
      options.find(
        (option) =>
          option.label.includes(label) || label.includes(option.label),
      )?.value ?? ''
    );
  }
  return '';
};

/** 滚动到指定类名的元素 */
export const scrollIntoViewWithOffset = (selector: string, offset: number) => {
  // const element = document.getElementById(id);
  const element = document.getElementsByClassName(selector)[0];
  if (element) {
    window.scrollTo({
      behavior: 'smooth',
      top:
        element.getBoundingClientRect().top -
        document.body.getBoundingClientRect().top -
        offset,
    });
  }
};

/** 获取 Record<string, string> 中的key */
export const getKeyByValue = (
  dataMap: Record<string, string>,
  value: string,
) => {
  if (!value || !dataMap) return '';
  let result = '';
  Object.keys(dataMap).forEach((key) => {
    if (dataMap[key] === value) {
      result = key;
    }
  });
  return result;
};

export const getUploadFilesLength = (uploadFiles: {
  [key: string]: Array<
    string | { path: string; ocrType: string; ocrCode: string }
  >;
}) => {
  let len = 0;
  Object.keys(uploadFiles).forEach((key) => {
    len += uploadFiles[key]?.length || 0;
  });
  return len;
};
