import { isOnlineProduction, request, Sentry } from '@elt_common/common';
/**
 * 对错误类扩充，符合我们接口返回的规范
 */
export class ResponseError extends Error {
  public code: number;

  public msg: string;

  public data: any;

  constructor({ code, msg, data }: { code: number; msg: string; data: any }) {
    super();
    this.code = code;
    this.msg = msg;
    this.data = data;
    // why: https://stackoverflow.com/questions/31626231/custom-error-class-in-typescript
    // Set the prototype explicitly.
    Object.setPrototypeOf(this, ResponseError.prototype);
  }
}

request.Fetch.getAxiosInstance().interceptors.response.use(
  (response: any) => {
    const serverTraceid = response.headers['x-b3-traceid'];
    Sentry.configureScope((scope) => {
      scope.setExtras({ serverTraceid });
    });
    return response;
  },
  (error) => {
    return Promise.reject(error);
  },
);

type ResType<T> = { code: number; msg: string; data: T };

/**
 * 封装 common 请求
 * @param method 支持的请求方法
 * @param url api url
 * @param data 参数
 * @param config 请求配置文件
 * @returns Promise<ResType<T>>
 */
export async function commonRequest<T>(
  method: 'getData' | 'postData' | 'getDataWithEncrypt' | 'postDataWithEncrypt',
  url: string,
  data: object = {},
  config = {},
) {
  // eslint-disable-next-line import/namespace
  return request[method]<ResType<T>>(url, data, config)
    .then((res: any) => {
      if (!isOnlineProduction && method.includes('WithEncrypt')) {
        // console.info('Decrypt Res Data ==>', res.data);
      }
      if (res.code === 0) {
        // 这里一定会存在 data 数据，规避 T｜ null 的类型，防止在代码里面的判断
        return res as ResType<T>;
      } else {
        throw new ResponseError(res);
      }
    })
    .catch(async (err: ResponseError) => {
      throw err;
    });
}

// 代理一层，不需要在项目中，通过 request.xxx 的方式调用方法
export function getData<T>(url: string, data: any = {}) {
  return commonRequest<T>('getData', url, data);
}
export function postData<T>(url: string, data: any = {}, config?: any) {
  return commonRequest<T>('postData', url, data, config);
}

export function getDataWithEncrypt<T>(url: string, data: any = {}) {
  if (!isOnlineProduction) {
    console.info('beforeEncryptdata: ', data);
  }
  return commonRequest<T>('getDataWithEncrypt', url, data, {
    isEncrypt: true,
  });
}
export function postDataWithEncrypt<T>(url: string, data: any = {}) {
  if (!isOnlineProduction) {
    // console.info('beforeEncryptdata: ', data);
  }
  return commonRequest<T>('postDataWithEncrypt', url, data, {
    isEncrypt: true,
  });
}
