import { useCallback } from 'react';
import { atom, useRecoilState, useResetRecoilState } from 'recoil';

import { IClaimSubmitState, IClaimSubmitSubSet } from '@/types/applySubmit';

const claimSubmitState = atom<IClaimSubmitState>({
  key: 'claimSubmitState',
  default: {
    signImg: '',
    templateData: '',
    isChecked: false,
    backToBottom: false,
  },
});

export const useClaimSourceStore = () => {
  const [claimSubmitData, setClaimSubmitData] =
    useRecoilState(claimSubmitState);
  const resetClaimSubmitState = useResetRecoilState(claimSubmitState);

  const updateClaimSubmitData = useCallback(
    (data: IClaimSubmitSubSet) => {
      setClaimSubmitData({
        ...claimSubmitData,
        ...data,
      });
    },
    [claimSubmitData, setClaimSubmitData],
  );
  return { claimSubmitData, updateClaimSubmitData, resetClaimSubmitState };
};
