import CryptoJS from 'crypto-js';

/*
 * @file: class of symmetric encryption
 * @desc: crypto-js提供的AES加/解密方法，ECB模式，PKCS7Padding填充
 * 需与后端约定同样方法解密
 * @Date: 2022/10/11
 */
class SymmetricEncrypt {
  constructor(key) {
    this.secretKey = key;
  }
  /**
   * 加密方法
   * @param {*} content
   * @returns
   */
  encrypt(content, secretKey) {
    const key = CryptoJS.enc.Utf8.parse(secretKey || this.secretKey);
    const srcs = CryptoJS.enc.Utf8.parse(content);
    const encrypted = CryptoJS.AES.encrypt(srcs, key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7
    });
    return encrypted.toString();
  }
  /**
   * 设置密钥
   * @param key 加密的key
   */
  setSecretKey(key) {
    this.secretKey = key;
  }
  /**
   * 解密方法
   * @param {*} content
   * @returns
   */
  decrypt(content, secretKey) {
    const key = CryptoJS.enc.Utf8.parse(secretKey || this.secretKey);
    const decrypt = CryptoJS.AES.decrypt(content, key, {
      mode: CryptoJS.mode.ECB,
      padding: CryptoJS.pad.Pkcs7
    });
    return CryptoJS.enc.Utf8.stringify(decrypt).toString();
  }
  /**
   * MD5 加密
   * @param content 需要加密的内容
   * @returns 加密的结果
   */
  static md5(content) {
    // eslint-disable-next-line @babel/new-cap
    return CryptoJS.MD5(content);
  }
}
export { SymmetricEncrypt as default };
