import { atom, useRecoilState } from 'recoil';

const claimSourceState = atom<string>({
  key: 'claimSourceState',
  default: '',
});

const envCodeState = atom<string>({
  key: 'envCodeState',
  default: '',
});

export const useClaimSourceStore = () => {
  const [claimSource, setClaimSource] = useRecoilState(claimSourceState);
  const [envCode, setEnvCode] = useRecoilState(envCodeState);
  return { claimSource, setClaimSource, envCode, setEnvCode };
};
