import {
  initReport,
  parseQuery,
  performanceMonitor,
  Sentry,
  tools,
} from '@elt_common/common';

import { getEnvName } from '@/utils';

// Sentry init
Sentry.init({});
Sentry.configureScope((scope) => {
  scope.setExtras({ currentCookie: document.cookie });
});

// 埋点公共参数 init
const query = parseQuery();
const reportExt = query.reportExt || JSON.stringify({});
try {
  const reportParam = reportExt
    ? { ...JSON.parse(decodeURIComponent(reportExt)) }
    : {};
  const { env_code, claim_source } = reportParam;
  reportParam.env_name = getEnvName(String(env_code));
  delete reportParam.env_code;
  if (!claim_source) {
    reportParam.claim_source = reportParam.env_name;
  }
  reportParam.from_share = Number(tools.checkFromShare());
  initReport(reportParam); // env_name  claim_source
} catch (error) {
  initReport({});
  Sentry.reportError('App入口文件：初始化埋点错误', { error });
  console.error('error', error);
}

// 可回溯初始化
// Isee.init();

// 初始化H5性能监控
performanceMonitor.init();
