import '@elt_common/components/base.css';
import './App.css';
import './services/init';

import { parseQuery, report, Sentry } from '@elt_common/common';
import {
  Empty,
  ErrorBoundary,
  FullPageLoading,
  Skeleton,
} from '@elt_common/components';
import {
  lazy,
  Suspense,
  useEffect,
  useLayoutEffect,
  useMemo,
  useState,
} from 'react';
import { Helmet } from 'react-helmet';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { RecoilRoot } from 'recoil';
import { StyleSheetManager } from 'styled-components';

import { useClaimSourceStore } from '@/store/claimSourceStore';
import { useLoginUserInfoStore } from '@/store/loginNewUserInfoStore';
import { getClaimSource } from '@/utils';

import { doClaimSysLogin } from './apis';
import { LoginStatus, urlWhiteList } from './const';
import useVenusConfig from './hooks/useVenusConfig';
import AgreementDetail from './views/AgreementDetail';

const Apply = lazy(() => import('./views/ApplyClaim'));
const ApplyGuide = lazy(() => import('./views/ApplyGuide'));
const ApplyRecord = lazy(() => import('./views/ApplyRecord'));
const ApplySubmit = lazy(() => import('./views/ApplySubmit'));
const BrandPromotion = lazy(() => import('./views/BrandPromotion'));
const ClaimDetail = lazy(() => import('./views/ClaimDetail'));
const ReportingSubmit = lazy(
  () => import('./views/ClaimNotifcation/ReportingSubmit'),
);
/** 商保直赔 */
const CommercialInsuranceClaim = lazy(
  () => import('./views/CommercialInsuranceClaim'),
);
const CommercialInsuranceClaimConfirm = lazy(
  () => import('./views/CommercialInsuranceClaim/confirm'),
);
const CommercialInsuranceClaimResult = lazy(
  () => import('./views/CommercialInsuranceClaim/result'),
);

const FileDownload = lazy(() => import('./views/FileDownload'));

const App = () => {
  const [loginAPIDone, setLoginAPIDone] = useState(false);
  const [loginError, setLoginError] = useState(false);
  const [, setUserInfo] = useLoginUserInfoStore();
  const { setClaimSource, setEnvCode } = useClaimSourceStore();

  const {
    config,
    commonConfigLoaded,
    commonConfigLoadFailed,
    updateCommonConfig,
  } = useVenusConfig();

  const currentPath = location.pathname;
  const isWhiteListUrl = urlWhiteList.some((url) =>
    currentPath.startsWith(url),
  );

  const pageLoading = useMemo(() => {
    return !isWhiteListUrl && (!commonConfigLoaded || !loginAPIDone);
  }, [commonConfigLoaded, isWhiteListUrl, loginAPIDone]);

  useEffect(() => {
    updateCommonConfig();
    const { claimSource, envCode } = getClaimSource();
    setClaimSource(claimSource);
    setEnvCode(envCode);
  }, []);

  // 小程序临时 code 换取用户信息
  useLayoutEffect(() => {
    const { tempCode } = parseQuery();
    (async () => {
      // 页面白名单，实现页面免登录，配置位置：@/const/index.ts
      if (!isWhiteListUrl) {
        try {
          const { data: loginUserInfo } = await doClaimSysLogin(tempCode ?? '');
          const { openid, unionid, isVerified } = loginUserInfo;
          setUserInfo(loginUserInfo);
          // 公参上报
          report.setExtendsInfo({
            openid,
            unionid,
            // uid: userId, uid 是sdk的预置字段，不要覆盖
            authentication_status: isVerified,
          });
          // 1-登录成功 2-未实名 3-登录失败
          if (Number(isVerified) !== LoginStatus.success) {
            setLoginError(true);
          }
        } catch (error) {
          setLoginError(true);
          Sentry.reportError('App入口文件：登陆接口异常', { error });
        } finally {
          setLoginAPIDone(true);
        }
      }
    })();
  }, []);

  if (pageLoading) {
    return <FullPageLoading loading />;
  }

  if (!isWhiteListUrl) {
    /** 系统升级中 */
    if (!config.commonFormOption.systemAvailable) {
      Sentry.reportMessage('系统升级');
      return <Empty text="系统升级中，请稍后尝试" />;
    }

    if (commonConfigLoadFailed) {
      return <Empty text="配置获取错误，请退出重试" />;
    }
    if (loginError) {
      return <Empty text="身份认证出错，请退出重试" />;
    }
  }

  return (
    <Suspense fallback={<Skeleton />}>
      <Switch>
        <Route exact path="/apply">
          <Helmet>
            <title>申请理赔</title>
          </Helmet>
          <Apply />
        </Route>
        {/* 兼容小程序缓存情况 */}
        <Route exact path="/policy-list">
          <Helmet>
            <title>理赔指引</title>
          </Helmet>
          <ApplyGuide />
        </Route>
        <Route exact path="/claim-detail">
          <Helmet>
            <title>理赔服务</title>
          </Helmet>
          <ClaimDetail />
        </Route>
        <Route exact path="/apply-submit">
          <Helmet>
            <title>确认材料</title>
          </Helmet>
          <ApplySubmit />
        </Route>
        <Route exact path="/report-submit">
          <Helmet>
            <title>理赔报案</title>
          </Helmet>
          <ReportingSubmit />
        </Route>
        <Route exact path="/apply-guide">
          <Helmet>
            <title>理赔指引</title>
          </Helmet>
          <ApplyGuide />
        </Route>
        <Route exact path="/apply-record">
          <Helmet>
            <title>申请记录</title>
          </Helmet>
          <ApplyRecord />
        </Route>
        <Route exact path="/agreement-detail">
          <Helmet>
            <title>授权与声明</title>
          </Helmet>
          <AgreementDetail />
        </Route>
        <Route exact path="/brand-promotion">
          <BrandPromotion />
        </Route>
        <Route exact path="/file-download/:pageType">
          <FileDownload />
        </Route>
        <Route exact path="/commercial-insurance-claim">
          <CommercialInsuranceClaim />
        </Route>
        <Route exact path="/commercial-insurance-claim/confirm">
          <CommercialInsuranceClaimConfirm />
        </Route>
        <Route exact path="/commercial-insurance-claim/result">
          <CommercialInsuranceClaimResult />
        </Route>
        <Route path="*">
          <Empty />
        </Route>
      </Switch>
    </Suspense>
  );
};

export default () => (
  <StyleSheetManager disableCSSOMInjection={true}>
    <BrowserRouter basename={window.APP_BASE_URL}>
      <ErrorBoundary>
        <RecoilRoot>
          <App />
        </RecoilRoot>
      </ErrorBoundary>
    </BrowserRouter>
  </StyleSheetManager>
);
