import { parseQuery } from '@elt_common/common';
import { Button, Layout } from '@elt_common/components';
import { useRealtimeCallback } from '@elt_common/hooks';
import { memo, useEffect, useMemo } from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';

import AntVersion from '@/components/AgreementDetail/AntVersion';
import CommonVersion from '@/components/AgreementDetail/CommonVersion';
import WeSureVersion from '@/components/AgreementDetail/WeSureVersion';
import useVenusConfig from '@/hooks/useVenusConfig';
import { useClaimSourceStore } from '@/store/claimSubmitStore';

const StyledContent = styled(Layout)`
  padding: 16px 16px calc(20px + 64px + env(safe-area-inset-bottom));
  background-color: var(--navyblue-10);
  color: var(--text-color-dark-80);
  font-family: var(--font-family);
  font-size: var(--content-3-font-size);
  font-weight: var(--font-weight-regular);
  letter-spacing: 0;
  line-height: var(--content-3-line-height);
`;
const Footer = styled(Layout.Footer)`
  background-color: var(--adm-color-white);
`;

const ButtonWrapper = styled(Button)`
  margin: 8px 16px;
  width: calc(100% - 32px);
`;

const ChannelCodeMap: any = {
  VGUZ5166: WeSureVersion, // 微保
  VGUZ5206: AntVersion, // 蚂蚁保
};

export default memo(() => {
  /** classCode 判断是否税优 */
  const { channel = '', classCode = '' } = parseQuery();
  const history = useHistory();
  const { claimSubmitData, updateClaimSubmitData } = useClaimSourceStore();
  let isSubmitBtnClicked = claimSubmitData.isChecked;

  const { config } = useVenusConfig();

  const backToSubmitPage = useRealtimeCallback(() => {
    // updateClaimSubmitData({
    //   isChecked: true,
    //   backToBottom: true,
    // });
    isSubmitBtnClicked = true;
    history.goBack();
  });

  const Info = useMemo(() => {
    const productInfo = {
      isShuiYou:
        classCode &&
        config.commonFormOption.shuiyouClassCode.includes(classCode),
    };
    const AgreementVersion = ChannelCodeMap[channel] || CommonVersion;
    return <AgreementVersion {...productInfo} />;
  }, [channel, classCode]);

  /** 监听浏览器返回 */
  useEffect(() => {
    const customBack = () => {
      updateClaimSubmitData({
        backToBottom: true,
        isChecked: isSubmitBtnClicked,
      });
      window.removeEventListener('popstate', customBack);
      history.goBack();
    };

    window.history.pushState(null, '');
    window.addEventListener('popstate', customBack);
    return () => {
      window.removeEventListener('popstate', customBack);
    };
  }, []);

  return (
    <StyledContent>
      {Info}
      <Footer>
        <ButtonWrapper onClick={backToSubmitPage}>我已阅读并同意</ButtonWrapper>
      </Footer>
    </StyledContent>
  );
});
